.house.loaded {

  header.jumbo {
    height:340px;
    position:relative;
    background-size:cover;

    .jumbo-contents {
      position:absolute;
      left:50px;
      bottom:30px;
      line-height: 2.5em;
      
      * {
        color:#fff;
        margin:8px 0 0 0;  
        padding:0;
        text-shadow:0 0 6px rgba(0,0,0,.9);
        font-family: 'Slabo 27px', serif;
      }

      h1 {
        font-size:40px;
      }

      h2 {
        font-size:30px;
        font-weight:normal;
      }
      
      .house-properties {
        font-size:26px;
      }
    }
  }

  main .section {
    padding:50px;
    font-size:17px;
    font-family: 'Nunito Sans', serif;

    h3 {
      font-size:24px;
      color:#cb5526;
    }
  }

}